.wrapper {
  padding-left: 10px;
  padding-right: 10px;
  min-height: calc(100vh - 121px);
}

article .wrapper .left_sadiebar {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(16.666666666666668% - 30px);
  box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
  -webkit-transform: translateX(0%);
  -ms-transform: translateX(0%);
  transform: translateX(0%);
  -webkit-transition: -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55), -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
  margin-top: 100px;
  border-radius: 5px;
}

article .wrapper .left_sadiebar_open {
  -webkit-transform: translateX(-400%);
  -ms-transform: translateX(-400%);
  transform: translateX(-400%);
  -webkit-transition: -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55), -webkit-transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}

article {
  background: #fafafb;
}

article .wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}

article .wrapper .content_part {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 30px);
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  flex-direction: column;
  margin: 0 auto;
  border-radius: 5px;
  padding-top: 100px;
}

article .wrapper .right_sidebar {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(16.666666666666668% - 30px);
  box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
  margin-top: 100px;
  border-radius: 5px;
}

@media screen and (max-width: 1280px) {
  article .wrapper .left_sadiebar {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;
    height: 100vh;
    width: 260px;
    background: #ffffff;
    -webkit-transform: translateX(-400%);
    -ms-transform: translateX(-400%);
    transform: translateX(-400%);
  }

  article .wrapper .left_sadiebar_open {
    -webkit-transform: translateX(0%);
    -ms-transform: translateX(0%);
    transform: translateX(0%);
  }

  article .wrapper .right_sidebar {
    display: none;
  }
}

@media screen and (max-width: 1536px) {
  .content_part {
    width: calc(66.66666666666667% - 30px);
  }
}

@media screen and (max-width: 1280px) {
  .content_part {
    width: calc(100% - 30px);
  }
}

@media screen and (max-width: 480px) {
  .header .wrapper .search_part {
    display: none;
  }
}
