

.header {
	background: #ffffff;	
	padding: 15px 0px;
	box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
	position: fixed;
	z-index: 9999;
 	width: 100%;

    .wrapper {
    	display: flex;
    	align-items: center;
		justify-content: space-between;
		height: 50px;

		.logo_part {
			margin-left: 15px;
			display: flex;
			align-items: center;
			
			img {
				margin-right: 30px;
				width: 35%;
			}
		}

		.search_part {


			form {
				position: relative;
				width: 350px;

				input {
					.p();
					width: 100%;
				    height: 40px;
				    padding: 5px 40px 5px 15px;
				    border-radius: 10px;
				    border: none;
				    background: #50b5ff40;
				    outline: none;
				    color: @color_grey;
				}
				a {
					position: absolute;
				    right: 15px;
				    top: 10px;
				    font-size: 22px;

					i {
						color: @color_sky_blue;
						font-size: 22px;
					}
				}
			}
		}

		.right_part {
			display: flex;
			margin-right: 50px;
			
			.notifications {
				margin-right: 20px;
				cursor: pointer;

				i {
					font-size: 22px;
					color: @color_sky_blue;
				}
			}
			.settings {
				cursor: pointer;

				i {
					font-size: 22px;
					color: @color_sky_blue;
				}
			}
		}
    }
}


article {
	background: @color_body_bg;

	.wrapper {
		display: flex;
		justify-content: space-between;

		.left_sadiebar {
			.col();
			.size(2);
			box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
			-webkit-transform: translateX(0%);
		    transform: translateX(0%);
		    transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
		    transition-delay: .1s;
		    margin-top: 100px;
		    border-radius: 5px;

			nav {


				ul {
					padding: 15px;
					width: 200px;

					li {
						.p();
						color: @color_grey;
						padding: 15px 50px 15px 15px;
						cursor: pointer;
						outline: none;

						a {
							text-decoration: none;
							color: @color_grey;

							i {
								font-size: 16px;
								margin-right: 15px;
							}
						}
						&:hover {
							color: @color_sky_blue;
							background: #f5fbff;
						}
						&:active {
							color: @color_sky_blue;
							background: #f5fbff;
						}
					}
				}
			}
		}
		.left_sadiebar_open {
			-webkit-transform: translateX(-400%);
		    transform: translateX(-400%);
		    transition: transform 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
		    transition-delay: .1s;
		}

		.profile_part {
			display: flex;
			.col();
			.size(7);
			.size-xxlpop(8);
			.size-xl(12);
			flex-direction: column;
			margin: 0 auto;
			border-radius: 5px;
			padding-top: 100px;

			.main_card {
				width: 100%;

				.main_card_cover_ava {
					position: relative;
					display: flex;
					justify-content: center;

					.img_cover {
						width: 100%;
						
						img {
							height: 400px;
							width: 100%;
							border-radius: 5px;
						}
					}
					.img_ava {
						position: absolute;
						top: 250px;
						display: flex;
						flex-direction: column;
						align-items: center;
						text-align: center;

						img {
							border: 5px solid #ffffff;
							border-radius: 100%;
							width: 200px;
							height: 200px;
						}
						h2 {
							.h1();
							color: @color_black;
							text-align: center;
							margin-top: 10px;
						}
					}
				}
				.main_card_footer {
					width: 100%;
					display: flex;
					background: @color_white;
					border-radius: 5px;
					padding-bottom: 50px;
					-webkit-box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
					box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);

					.left {
						width: 50%;
						
						a {
							img {
								border-radius: 5px !important;
								margin: 15px 7px;
							}
						}
					}
					.right {
						width: 50%;
						display: flex;
						align-self: center;
						justify-content: flex-end;
						text-align: center;

						.right_item {
							margin: 15px 7px;

							.right_item_title {
								.h3();
								font-weight: 500;
								color: @color_black;
							}
							.right_item_data {
								.p();
								color: @color_black;
							}
						}
					}
				}
			}

			.profile_menu {
				margin-top: 15px;

				.tabs {

					.tabs__nav {
						display: flex;

						.tabs-nav__item {
							width: 25%;
							text-align: center;
							padding: 20px 15px;
							.p();
							background: @color_white;
							color: @color_black;
							cursor: pointer;
						}
						.is-active {
							color: #ffffff;
							background: @color_sky_blue;
							border-radius: 5px;
						}
					}
					.tabs__content {
						margin-top: 15px;

						.tab {
							display: none;
							color: @color_black;
						}
						.tab-1 {
							display: none;
							justify-content: space-between;

							.tab-1_leftSidbar {
								width: calc(29% - 10px);

								.tab-1_leftSidbar_photos {
									display: flex;
									flex-direction: column;
									padding: 10px;
									background: #ffffff;
									border-radius: 5px;
									box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);

									.leftSidbar_photos_header {
										display: flex;
										justify-content: space-between;

										h5 {
											.h2_nav();
											color: @color_black;
										}
										p {
											.p();
											color: @color_sky_blue;
										}
									}
									.gallary {
										
										ul {
											display: flex;
											justify-content: space-between;
											flex-wrap: wrap;

											li {
												width: calc(33% - 5px);
												height: 90px;

												a {

													img {
														height: 75px;
														margin: 0 auto;
													}
												}
											}
										}
									}
								}
							}
							.tab-1_timelineContent {
								width: calc(69% - 10px);

								.create_post {
									padding: 10px;
									background: #ffffff;
									border-radius: 5px;
									box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);

									.create_post_header {

										h5 {
											.h2_nav();
											color: @color_black;
										}
									}
									hr {
										margin-top: 15px;
										margin-bottom: 15px;
									}
									.input_text {
										display: flex;
										margin-top: 10px;
										margin-bottom: 10px;

										.profileAva {
											width: 19%;

											img {
												width: 75px;
												height: 75px;
												border-radius: 50%;
												box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
											}
										}
										
										textarea {
											width: 79%;
											height: 50px;
											.p();
											color: #000000;
											padding: 5px;
											border: none;
											outline: none;
										}
									}
									.btns {
										display: flex;

										input {
											margin-left: auto;
											.p();
											color: #ffffff;
											background: @color_sky_blue;
											padding: 7px 30px;
											cursor: pointer;
											outline: none;
											border: none;
											border-radius: 5px;

											&:hover {
												background: transparent;
												border: 1px solid @color_sky_blue;
												color: @color_sky_blue;
												padding: 6px 29px;
											}
										}
									}
								}
								.single_post {
									padding: 10px;
									background: #ffffff;
									border-radius: 5px;
									box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
									margin-top: 15px;

									.single_post_header {
										display: flex;
										align-items: center;

										.single_post_header_foto {

											a {

												img {
													width: 75px;
													height: 75px;
													border-radius: 50%;
												}
											}
										}
										.single_post_header_title {
											margin-left: 15px;

											h6 {
												.pxl();
												color: #000000;

												a {
													text-decoration: none;
													color: #000000;
												}
											}
											p {
												.ps();
												color: @color_grey;
											}
										}
									}
									.single_post_content {
										margin-top: 15px;

										p {
											.p();
											color: #000000;
										}
										img {
											width: 100%;
											height: auto;
										}
									}
									.single_post_info {
										margin-top: 15px;
										display: flex;

										.likes {
											display: flex;
											align-items: center;
											i {
												margin-right: 10px;
												color: @color_sky_blue;
											}
											p {
												.p();
												color: @color_grey;
											}
										}
										.comments {
											margin-left: 15px;

											p {
												.p();
												color: @color_grey;
											}
										}
										.share {
											margin-left: auto;
											display: flex;
											align-items: center;

											i {
												margin-right: 10px;
												color: @color_sky_blue;
											}
											p {
												.p();
												color: @color_grey;
											}
										}
									}
									hr {
										margin-top: 15px;
										margin-bottom: 15px;
									}
									.comments_area {

										.single_comments {

											.comments_content_header {
												display: flex;
												align-items: center;

												.single_comments_foto {

													a {
														img {
															width: 75px;
															height: 75px;
															border-radius: 50%;
														}
													}
												}

												p {
													.p();
													color: #000000;
													margin-left: 15px;
												}
											}
											.single_comments_content {
												margin-top: 15px;
												margin-bottom: 15px;

												.text {
													margin-top: 15px;
													margin-bottom: 15px;

													p {
														.p();
														color: #000000;
													}
												}
												.comments_content_footer {
													display: flex;

													p {
														.p();
														color: @color_sky_blue;
														margin-right: 15px;

														&:hover {
															color: #000000;		
														}
													}
													.date {
														color: @color_grey;
														margin-left: auto;

														&:hover {
															color: @color_grey;		
														}
													}
												}
											}
											.single_comments_addComment {
												margin-top: 15px;

												form {
													width: 100%;
													display: flex;
													flex-wrap: wrap;
													margin-top: 30px;

													textarea {
														width: 98%;
														.p();
														color: #000000;
														padding: 5px;
														border: 1px solid @color_grey;
														outline: none;
													}
													input {
														margin-left: auto;
														margin-top: 15px;
														bottom: 5px;
													    right: 12px;
													    padding: 9px 15px;
													    background: @color_sky_blue;
													    color: #ffffff;
													    border-radius: 5px;
													    border: none;
													    outline: none;
													    cursor: pointer;

													    &:hover {
													    	background: transparent;
													    	color: @color_sky_blue;
													    	border: 1px solid @color_sky_blue;
													    	padding: 8px 14px;
													    }
													}
												}
											}
										}
									}
								}
							}
						}
						.tab-2 {
							display: none;
							justify-content: space-between;
							background: @color_white;

							.tab_submenu {
								width: 28%;
								margin-top: 25px;

								.tab_submenu_item {
									.p();
									color: #000000;
									text-align: center;
									padding: 10px;
									cursor: pointer;
								}
								.is_active {
									background: #a3d8ff;
								}
							}
							.tab_contents {
								width: 68%;

								.tab_content {

									h3 {
										.h2_nav();
										color: #000000;
										margin-top: 30px;
									}
									hr {
										margin-bottom: 15px;
										margin-top: 15px;
									}
									.tab_content_info_data {
										display: flex;
										justify-content: space-between;
										margin-bottom: 10px;

										.title {
											width: 28%;

											h5 {
												.p();
												color: #000000;
											}
										}
										.value {
											width: 68%;

											p {
												.p();
												color: #000000;
											}
										}
									}
								}
							}
						}
						.tab-3 {
							display: none;
						}
						.tab-4 {
							display: none;
						}
						.is-active {
							display: flex;
						}
					}
				}
			}
		}

		.right_sidebar {
			.col();
			.size(2);
			box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
			margin-top: 100px;
			border-radius: 5px;
			
			.friends_online_list {
				padding: 15px;
				width: 200px;

				.friends_online_list_item {
					margin-top: 20px;
					margin-bottom: 20px;

					a {
						display: flex;
						width: 100%;
						text-decoration: none;

						.friends_online_ava {
							width: calc(30% - 10px);
							margin-right: 15px;
							position: relative;

							.rounde_circle {
								border-radius: 50% !important;
							}

							&:before {
								content: '';
							    position: absolute;
							    border-radius: 100%;
							    z-index: 1;
							    border: 2px solid @color_white;
							    background-color: green;
							    height: 16px;
							    width: 16px;
							    right: 0;
							    bottom: -3px;
							}
						}
						.friends_online_info {
							width: calc(68% - 10px);
							.p();

							.name {
								color: @color_black;

								&:hover {
									color: @color_sky_blue;
								}
							}
							.role {
								color: @color_grey;
							}
						}
					}
				}
			}
		}
	}
}




.footer {
	background: #000000;
	padding-top: 25px;
	padding-bottom: 25px;
	margin-top: 50px;
	width: 100%;

	.wrapper {
		text-align: center;
		color: #ffffff;
	}	
}


/*Mob nav*/

/*----------------- button ---------*/
	.button_new {
		// position: absolute;
		// top: 30px;
		// right: 0;
		// left: 100px;
		// z-index: 1000;
		width: 35px;
    	height: 25px;
		overflow:hidden;
	  	cursor:pointer;
	  	background: transparent;
		display: flex;
	    flex-direction: column;
	    justify-content: space-between;
	}

	.button_new:hover {
	  	cursor:pointer;
	}

	.button_new > .line {
		width: 100%;
		height: 3px;
		background: @color_grey;
	}

	.button_new .line:nth-child(1), .button .line:nth-child(3){
	  -webkit-transform:rotate(0deg) translateY(0);
	  -moz-transform:rotate(0deg) translateY(0);
	  ttransform:rotate(0deg) translateY(0);
	    -webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	  transition:         all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	}

	.button_new .line:nth-child(2){
	  -webkit-transform:translateX(0);
	  -moz-transform:translateX(0);
	  transform:translateX(0);
	   -webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	  transition:         all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	}


	/*-------------- the close button ---------*/

	.close .line:nth-child(1) {
		-webkit-transform:rotate(45deg) translateY(15px);
		-moz-transform:rotate(45deg) translateY(15px);
		transform:rotate(45deg) translateY(15px);
	    margin-left: 15px;
	  
	}

	.close .line:nth-child(3) {
		-webkit-transform:rotate(-45deg) translateY(-15px);
		-moz-transform:rotate(-45deg) translateY(-15px);
		transform:rotate(-45deg) translateY(-15px);
		margin-left: 15px;
	}

	.close .line:nth-child(2){
	  -webkit-transform:translateX(-100px);
	  -moz-transform:translateX(-100px);
	  transform:translateX(-100px);
	}
	/*End Mob nav*/


