@media screen and (max-width: 1280px) {
	article .wrapper .left_sadiebar {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 9999;
		height: 100vh;
		width: 260px;
		background: #ffffff;
		-webkit-transform: translateX(-400%);
		transform: translateX(-400%);
	}
	article .wrapper .left_sadiebar_open {
		-webkit-transform: translateX(0%);
		transform: translateX(0%);
	}
	article .wrapper .right_sidebar {
		display: none;
	}
	article .wrapper .profile_part .profile_menu .tabs .tabs__content .tab-1 
